<!-- 模型： DOM 结构 -->
<template>
    <!-- 年报信息 -->
    <div class="annualReport">
        <div class="ic-basic">
            <template v-if="isajax">
                <!-- 有数据 -->
                <template v-if="list&&list.length>0">
                     <table class="table-content">
						<tr>
							<td style="width:72px">序号</td>
							<td >标题</td>
							<td style="width:200px">操作</td>
						</tr>
						<tr v-for="(el,i) in list" :key="i">
							<td>{{i+1}}</td>
							<td>{{el.years}}年度报告</td>
							<td> <span @click="seeDetail(el)" class="see-detail">查看详情</span></td>
						</tr>
					</table>
                </template>
                <!-- 无数据 -->
                <template v-else-if="list&&list.length==0">
                    <div class="no-toast">
                        <img src="../../assets/img/bank_card @2x.png" alt="">
                        <span>Sorry，该企业暂无年报信息</span>
                    </div>
                </template>
                <template v-else-if="!list">
                    <div class="ajax-erroe">
                        <img src="../../assets/img/pic-zoudiu.png"/>
                        <!-- <span @click="recoldFn">刷新</span> -->
                    </div>
                </template>
            </template>
            <template v-else>
                <div style="min-height:240px" v-loading="loading" element-loading-text="拼命加载中"></div>
            </template>
        </div>
        <!-- 弹窗 -->
            <v-annual-detail v-if="mask" :data="detail"></v-annual-detail>
    </div>
</template>
<script>
import popup from '@/components/popup'
import annualDetail from '@/components/business/annualDetail'
export default {
    name: 'annualReport', // 基本信息
    data() {
        return {
            // 数据模型
            mask:false,
            detail:'',
            list:[],
            loading:true,
            isajax:false
        }
    },
    watch: {
        // 监控集合
    },
    props: {
        // 集成父级参数
        // list:{
            
        // }
    },
    inject:['reload'],
    components:{
        'v-pop':popup,
        'v-annual-detail':annualDetail
    },
    computed:{
    },
    beforeCreate() {
        // console.group('创建前状态  ===============》beforeCreate');
    },
    created() {
        // console.group('创建完毕状态===============》created');
        this.getYears()
    },
    mounted() {
        // console.group('挂载结束状态===============》mounted');
        this.$nextTick(function() {
            // console.log('执行完后，执行===============》mounted');
        });
    },
    methods: {
        // 方法 集合
        seeDetail(el){
            let that=this;
            this.$http({
                method:'post',
                url:'/gs/report/detail',
                data:{
                    comId:el.comId,
                    years:el.years
                }
            }).then(function(res){
                if(res.data.code==1){
                    that.mask=true;
                    that.modalHelper.afterOpen();
                    that.detail=res.data.data;
                }else{
                    that.$alert(res.data.msg);
                }
            })
            
        },
        //刷新
        recoldFn(){
            this.reload();
        },
        getYears(){//企业年报
            let that=this;
            this.$http({
                method:'post',
                url:'/gs/report/years',
                data:{
                    comId:this.$route.query.id,
                }
            }).then(function(res){
                that.isajax=true;
                if(res.data.code==1){
                    that.list=res.data.data;
                }else{
                    that.$alert(res.data.msg);
                }
            }).catch(req =>{
                that.isajax=true;
                that.list=null;
            })
        }, 
    }

}

</script>
<!-- 增加 "scoped" 属性 限制 CSS 属于当前部分 -->
<style  lang='less' scoped>
.ic-basic {
    // border: 1px solid #f2f2f2;
    box-sizing: border-box;
     .table-content {
        width: 844px;
        tr {
            td {
                padding: 0 10px;
            }
        }
    }
    .see-detail{
        color:#4494F0;
        cursor: pointer;
        text-decoration: underline;
    }
    .loading{
        cursor: wait;
    }
}

</style>



